/* Simple box container without green border */
.box-container {
  border: 1px solid #ccc; /* Light gray border */
  padding: 20px; /* Space inside the box */
  border-radius: 10px; /* Rounded corners */
  max-width: 800px; /* Limit the width of the box */
  margin: 20px auto; /* Center the box on the page */
  background-color: #f9f9f9; /* Light background color inside the box */
}

/* Basic question container styles */
.basic-questions-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-title {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.rating-description {
  font-size: 1rem;
  margin-bottom: 1rem;
}

/* Progress bar styles */
.progress-bar-container {
  width: 80%;
  height: 10px;
  background-color: #e0e0e0;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  border-radius: 5px;
}

/* Question item styles */
.question-item {
  margin-bottom: 1rem;
  text-align: center;
}

.question-text {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

/* Slider styles */
.slider {
  width: 100%; /* Make the slider as wide as the container */
  max-width: 600px; /* Limit the slider's maximum width */
  margin: 1rem 0;
  appearance: none;
}

.slider::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 16px;
  background-color: #4caf50;
  border-radius: 50%;
  cursor: pointer;
}

.slider-labels {
  display: flex;
  justify-content: space-between; /* Align labels under each value */
  width: 100%;
  max-width: 600px; /* Match the sliderâ€™s width */
  margin-top: 0.5rem;
}

.slider-labels span {
  font-size: 1rem;
}

/* Navigation button styles */
.navigation-buttons {
  display: flex;
  gap: 10px;
}

/* Submit button styles */
.submit-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
